@import "https://fonts.googleapis.com/css2?family=Inter:wght@400;500;600&display=swap";
*, :before, :after {
  box-sizing: border-box;
}

ul[class], ol[class] {
  padding: 0;
}

body, h1, h2, h3, h4, p, ul[class], ol[class], li, figure, figcaption, blockquote, dl, dd {
  margin: 0;
}

body {
  scroll-behavior: smooth;
  text-rendering: optimizespeed;
  color: var(--primary-title-color);
  background-color: var(--primary-bg);
  min-height: 100vh;
  font-family: Inter, sans-serif;
}

ul[class], ol[class] {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

input, button, textarea, select {
  font: inherit;
}

@media (prefers-reduced-motion: reduce) {
  * {
    scroll-behavior: auto !important;
    transition-duration: .01ms !important;
    animation-duration: .01ms !important;
    animation-iteration-count: 1 !important;
  }
}

.container {
  justify-content: center;
  max-width: 375px;
  margin-left: auto;
  margin-right: auto;
  padding-left: 20px;
  padding-right: 20px;
  display: flex;
}

@media screen and (width >= 375px) {
  .container {
    min-width: 375px;
    max-width: none;
  }
}

@media screen and (width >= 768px) {
  .container {
    min-width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (width >= 1280px) {
  .container {
    min-width: 1280px;
    padding-left: 100px;
    padding-right: 100px;
  }
}

.link {
  text-decoration: none;
}

.header-btn-box {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header {
  padding-top: 24px;
}

.header-container {
  display: none;
}

.header-nav {
  gap: 16px;
  display: flex;
}

.mob-container {
  justify-content: space-between;
  display: flex;
}

.header-nav-link {
  color: var(--primary-title-color);
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: normal;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.header-nav-link:hover, .header-nav-link:focus {
  color: var(--primary-brend);
  transform: scale(1.2);
}

.is-active-link {
  color: var(--primary-brend);
}

.header-logo {
  align-self: center;
}

.header-logo-link {
  color: var(--primary-title-color);
  letter-spacing: -.64px;
  font-family: Inter;
  font-size: 16px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.12;
}

.header-span {
  color: var(--primary-brend);
}

.header-buttons {
  align-items: center;
  gap: 14px;
  display: flex;
}

.header-cart-btn {
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
}

.header-cart {
  stroke: var(--primary-title-color);
  fill: #0000;
  width: 24px;
  height: 24px;
}

.header-burger-btn {
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  width: 32px;
  height: 32px;
  padding: 0;
  display: flex;
}

.header-burger {
  fill: var(--primary-title-color);
  width: 32px;
  height: 32px;
}

.header-burger:hover, .header-burger:focus {
  fill: var(--primary-brend);
}

.header-cart:hover, .header-cart:focus {
  stroke: var(--primary-brend);
}

.headerModal {
  z-index: 9999;
  display: none;
  position: fixed;
  top: 0;
  right: 0;
}

.header-modal-content {
  background-color: var(--primary-bg-mob);
  justify-content: center;
  width: 53vw;
  height: 100vh;
  display: flex;
}

.header-mobile-nav {
  flex-direction: column;
  align-items: center;
  gap: 16px;
  margin-top: 30vh;
  display: flex;
}

.header-mobile-link {
  color: #fff;
  font-family: Inter;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
}

.header-close {
  line-height: inherit;
  color: #fff;
  cursor: pointer;
  font-size: 40px;
  position: absolute;
  top: 10px;
  right: 20px;
}

.header-switch-mob {
  width: 46px;
  height: 20px;
  display: inline-block;
  position: absolute;
  bottom: 20px;
  left: 20px;
}

.header-switch {
  width: 46px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header-switch .header-input, .header-switch-mob .header-input-mob {
  display: none;
}

.header-slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  inset: 0;
}

.header-slider:before {
  content: "";
  background-color: #fff;
  width: 20px;
  height: 20px;
  transition: all .4s;
  position: absolute;
}

.header-input:checked + .header-slider, .header-input-mob:checked + .header-slider {
  background-color: var(--primary-brend);
}

.header-input:checked + .header-slider:before, .header-input-mob:checked + .header-slider:before {
  transform: translateX(26px);
}

.header-slider.round {
  border-radius: 34px;
}

.header-slider.round:before {
  border-radius: 50%;
}

.no-animation {
  transition: none !important;
}

@media screen and (width >= 768px) {
  .header {
    padding-top: 30px;
  }

  .mob-container {
    display: none;
  }

  .header-container {
    justify-content: space-between;
    display: flex;
  }

  .header-nav-container {
    align-self: center;
  }

  .header-buttons {
    gap: 18px;
  }
}

@media screen and (width >= 1200px) {
  .header {
    padding-top: 40px;
  }

  .header-buttons {
    gap: 24px;
  }
}

#hero-section {
  justify-content: center;
  display: flex;
}

.hero-container {
  flex-direction: column;
  justify-content: center;
  padding-top: 92px;
  padding-bottom: 42px;
  display: flex;
}

.hero-block {
  margin-left: auto;
  margin-right: auto;
}

.hero-title {
  text-transform: uppercase;
  margin-bottom: 14px;
  font-family: Inter;
  font-size: 26px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.31;
}

.hero-span {
  color: var(--primary-brend);
  font-style: italic;
  font-weight: 400;
}

.hero-descr {
  color: var (--primary-title-color);
  margin-bottom: 24px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.28;
}

.hero-btn {
  color: var(--primary-title-color);
  background-color: inherit;
  background-color: #0000;
  border: 1px solid #9bb537;
  border-radius: 12px;
  justify-content: center;
  align-items: center;
  gap: 10px;
  width: 111px;
  height: 37px;
  margin-bottom: 48px;
  font-family: Inter;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: normal;
