}

.hero-btn:hover, .hero-btn:focus {
  background: var(--primary-brend);
  color: var(--primary-btn-color);
}

.hero-wrap {
  width: 335px;
  margin-bottom: 28px;
}

.swiper-container {
  gap: 8px;
  display: flex;
}

.images {
  width: 100%;
  display: flex;
}

.image {
  background-color: #050505;
  height: 280px;
}

.image:not(:last-child) {
  border-radius: 10px;
}

.cook > img {
  object-fit: cover;
  border-radius: 10px;
  width: 80px;
  height: 280px;
}

.dish {
  width: 200px;
  position: relative;
}

.dish > img {
  background: radial-gradient(circle, #9bb53780 0%, #0505054d 75%);
  width: 150px;
  height: 150px;
  margin-bottom: 70px;
  translate: 15% 35%;
}

.dish-zoom {
  border-radius: 10px 0 0 10px;
  overflow: clip;
}

.dish-zoom > img {
  object-position: -165px -80px;
  object-fit: cover;
  width: 60px;
  height: 442px;
}

.dish-descr {
  text-align: center;
  flex-direction: column;
  display: flex;
}

.dish-name {
  color: #f8f8f899;
  text-align: center;
  text-transform: uppercase;
  max-width: 144px;
  margin: auto;
  font-size: 10px;
  font-style: normal;
  font-weight: 500;
  line-height: 14px;
}

.dish-area {
  color: #f8f8f833;
  text-align: center;
  width: 24px;
  margin-top: 4px;
  margin-left: auto;
  margin-right: auto;
  font-size: 8px;
  font-style: normal;
  font-weight: 400;
  line-height: 12px;
  display: flex;
}

.swiper {
  width: 335px;
  height: 320px;
}

.swiper-container {
  margin-left: auto;
}

.swiper-pagination-bullet {
  text-align: center;
  letter-spacing: 2px;
  background-color: gray;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.swiper-pagination-bullet-active {
  background-color: var(--primary-brend);
  letter-spacing: 2px;
  font-family: Poppins;
  font-size: 25px;
  font-style: normal;
  font-weight: 600;
  line-height: 18px;
}

.swiper-horizontal > .swiper-pagination-bullets, .swiper-pagination-bullets.swiper-pagination-horizontal, .swiper-pagination-custom, .swiper-pagination-fraction {
  width: 360px;
}

@media screen and (width >= 768px) {
  .hero-container {
    flex-direction: column;
    max-width: none;
    padding-bottom: 80px;
    padding-left: 32px;
  }

  .hero-block {
    margin-left: 0;
    margin-right: 0;
  }

  .hero-wrap {
    margin-bottom: 32px;
  }

  .hero-title {
    width: 446px;
    margin: 0 0 16px;
    font-size: 36px;
  }

  .hero-descr {
    width: 408px;
    font-size: 16px;
    line-height: 24px;
  }

  .hero-btn {
