    height: 45px;
    margin-bottom: 64px;
  }

  .swiper {
    width: 736px;
    height: 490px;
  }

  .swiper-container {
    gap: 16px;
  }

  .cook > img {
    width: 137px;
    height: 442px;
  }

  .dish {
    width: 351px;
    height: 442px;
  }

  .dish > img {
    width: 262px;
    height: 262px;
    margin-bottom: 102px;
    translate: 15% 25%;
  }

  .dish-zoom {
    border-radius: 10px 0 0 10px;
    height: 442px;
    overflow: clip;
  }

  .dish-zoom > img {
    object-position: -80px -10px;
    object-fit: cover;
    width: 216px;
    transform: scale(1.3);
  }

  .dish-name {
    max-width: 276px;
    font-size: 14px;
    line-height: 18px;
  }

  .dish-area {
    margin-top: 8px;
